import React, { useState, useEffect } from 'react';
import AppContext from './AppContext';
import '@metamask/legacy-web3';

function Provider({ children }) {
  const [key, setKey] = useState('token');
  const [tokenData, setTokenData] = useState({});
  const [jwt, setJwt] = useState('');
  const [ethereum, setEthereum] = useState({});
  const [accounts, setAccounts] = useState([]);

  useEffect(() => {
    if (window.ethereum) {
      setEthereum(window.ethereum);
      window.web3.version.getNetwork((e, netId) => {
        if (!e) {
          if (Number(netId) !== 3) return alert('Mude a rede para Ropten antes de continuar!');
          window.ethereum.enable()
            .then(() => {
              setAccounts(window.web3.eth.accounts);
            })
            .catch(({ message }) => {
              alert(`${message}\nPor favor, conecte-se com uma carteira válida.`);
            });
        }
      });
    }
  }, []);

  const data = {
    accounts,
    ethereum,
    jwt,
    key,
    setJwt,
    setKey,
    setTokenData,
    tokenData,
  };

  return (
    <AppContext.Provider value={data}>
      {children}
    </AppContext.Provider>
  );
}

export default Provider;